<template>
  <div class="apitest">
    <b-container class="bv-example-row">
      <b-row class="mt-5">
        <b-col cols="18" md="12">
          <SearchBar ref="SearchBar" :msg="serarch_Content" v-model="serarch_Content"></SearchBar>
        </b-col>
      </b-row>
      
      <b-row class="mt-5 mb-5" v-show="isVisable">
        <b-col cols="18" md="12">
            <div class="d-flex justify-content-center">
              <b-spinner label="Loading..."></b-spinner>
            </div>
        </b-col>
      </b-row>

      <div id="my-table" v-for="item in detailsList" v-bind:key="item.detailId" v-show="!isVisable">
        <SearchResultItem :item="item" :currentPage="currentPage" class="resItemStyle"/>
      </div>
      
      <b-row class="mt-3">
        <b-col cols="18" md="12">
          <div class="mr-0">
            <b-pagination
              v-model="currentPage"
              :total-rows="total"
              :per-page="perPage"
              :hide-goto-end-buttons="true"
              class="mt-4"
              align="center"
              v-if="total != 0"
              @input="onTouch"
            >
              <span class="text-dark" slot="prev-text">上一页</span>
              <span class="text-dark" slot="next-text">下一页</span>
              <div slot="ellipsis-text">
                <b-spinner small type="grow"></b-spinner>
                <b-spinner small type="grow"></b-spinner>
                <b-spinner small type="grow"></b-spinner>
              </div>
              <span slot="page" slot-scope="{ page, active }">
                <b v-if="active">{{ page }}</b>
                <i v-else>{{ page }}</i>
              </span>
            </b-pagination>
          </div>
        </b-col>
      </b-row>
    </b-container>   
  </div>
</template>

<script>
import SearchBar from '@/components/SearchBar.vue'
import SearchResultItem from '@/components/SearchResultItem'

export default {
  name: 'apitest', //name为文件名的全小写
  data () {
    return{
      detailsList: '',
      search_id: '',
      serarch_Content: this.$route.params.searchContent,
      isVisable:true,
      currentPage: sessionStorage.getItem("currentPage"),
      total: 0,
      perPage: 5,
      isFirstPage: true,
      isLastPage: false
    }
  },
  created () {
    this.currentPage = sessionStorage.getItem("currentPage")
    switch(this.$route.params.searchContent) {
      case "抗静电剂":
          this.search_id = 1;
          break;
      case "阻燃剂":
          this.search_id = 2;
          break;
      case "抗菌剂":
          this.search_id = 3;
          break;
      case "柔软剂":
          this.search_id = 4;
          break;
      case "抗皱整理剂":
          this.search_id = 5;
          break;
      case "抗紫外线剂":
          this.search_id = 6;
          break;
      case "防水防油整理剂":
          this.search_id = 7;
          break;
     default:
        this.search_id = null
    }

    if(this.search_id == null){
      this.$router.push({ path: '/'});
    }
    var api = `https://www.domyown.top/ssm/details/${this.search_id}`
    this.axios
      .get(api,{
        params: {
          pn: this.currentPage
        }
      })
      .then(response => {
        this.isVisable = false
        this.detailsList = response.data.extend.pageInfo.list
        this.total = response.data.extend.pageInfo.total
        this.isFirstPage = response.data.extend.pageInfo.isFirstPage
        this.isLastPage = response.data.extend.pageInfo.isLastPage
      })
      .catch(() => (
        this.$router.push({ path: '/'})
      ))
  },
  beforeRouteUpdate(to, from, next){
    this.isVisable = true
    next()
    this.doSearch()
  },
  methods:{
    doSearch () {
      switch(this.$route.params.searchContent) {
        case "抗静电剂":
          this.search_id = 1;
          break;
        case "阻燃剂":
          this.search_id = 2;
          break;
        case "抗菌剂":
          this.search_id = 3;
          break;
        case "柔软剂":
          this.search_id = 4;
          break;
        case "抗皱整理剂":
          this.search_id = 5;
          break;
        case "抗紫外线剂":
          this.search_id = 6;
          break;
        case "防水防油整理剂":
          this.search_id = 7;
          break;
        default:
        this.search_id = null
      }

      if(this.search_id == null){
        this.$router.push({ path: '/'});
      }
      var api = `https://www.domyown.top/ssm/details/${this.search_id}`
      this.axios
        .get(api,{
          params: {
            pn: 1
          }
        })
        .then(response => {
          this.isVisable = false
          this.detailsList = response.data.extend.pageInfo.list
          this.total = response.data.extend.pageInfo.total
          this.isFirstPage = response.data.extend.pageInfo.isFirstPage
          this.isLastPage = response.data.extend.pageInfo.isLastPage
        })
        .catch(() => (this.$router.push({ path: '/'})))
      },
      onTouch () {
        sessionStorage.setItem("currentPage",this.currentPage);
        switch(this.$route.params.searchContent) {
        case "抗静电剂":
          this.search_id = 1;
          break;
        case "阻燃剂":
          this.search_id = 2;
          break;
        case "抗菌剂":
          this.search_id = 3;
          break;
        case "柔软剂":
          this.search_id = 4;
          break;
        case "抗皱整理剂":
          this.search_id = 5;
          break;
        case "抗紫外线剂":
          this.search_id = 6;
          break;
        case "防水防油整理剂":
          this.search_id = 7;
          break;
        default:
          this.search_id = null
        }
        this.isVisable = true
        if(this.search_id == null){
          this.$router.push({ path: '/'});
        }
        var api = `https://www.domyown.top/ssm/details/${this.search_id}`
        this.axios
          .get(api,{
            params: {
              pn: this.currentPage
            }
          })
          .then(response => {
            this.isVisable = false
            this.detailsList = response.data.extend.pageInfo.list
            this.total = response.data.extend.pageInfo.total
            this.isFirstPage = response.data.extend.pageInfo.isFirstPage
            this.isLastPage = response.data.extend.pageInfo.isLastPage
          })
          .catch(() => (this.$router.push({ path: '/'})))
      },
  },
  components: {
    SearchBar,
    SearchResultItem
  }
}
</script>

<style lang="less" scoped>
.resItemStyle{
  border-bottom: 1px #eee solid;
}
</style>